body {
  box-sizing: content-box !important; }

*,
*:before,
*:after {
  box-sizing: inherit !important; }

.f-select {
  position: relative;
  font-size: inherit;
  text-align: left; }
  .f-select *,
  .f-select *:before,
  .f-select *:after {
    box-sizing: border-box; }
  .f-select__inner {
    display: block;
    position: relative; }
  .f-select__field {
    color: inherit;
    padding: 1em 0.9em;
    width: 100%;
    border: 0;
    caret-color: #6fcf97;
    font-size: 1em;
    font-family: inherit;
    line-height: initial;
    appearance: none;
    padding-right: 2.7em; }
    .f-select__field:focus {
      outline: 0; }
    .f-select__field.f-select__field[disabled] {
      color: silver; }
    .has-error .f-select__field {
      caret-color: #e91e63; }
    .f-select__field::-moz-selection {
      color: white;
      background: #6fcf97 !important;
      text-shadow: none; }
    .f-select__field::selection {
      color: white !important;
      background: #6fcf97 !important;
      text-shadow: none; }
    .has-error .f-select__field::-moz-selection {
      color: white;
      background: #e91e63 !important;
      text-shadow: none; }
    .has-error .f-select__field::selection {
      color: white !important;
      background: #e91e63 !important;
      text-shadow: none; }
    .f-select__field:-webkit-autofill {
      -webkit-text-fill-color: inherit !important; }
  .f-select__label {
    user-select: none;
    font-size: 1em; }
  .f-select__label-text {
    color: gray; }
    .f-select__field[disabled].f-select__field[disabled] + .f-select__label .f-select__label-text {
      color: silver; }
  .f-select__indicator {
    pointer-events: none;
    user-select: none;
    color: silver; }
    .f-select__indicator--optional {
      font-size: 0.5em; }
    .f-select__indicator--required {
      font-size: 1em; }
    .has-error .f-select__field:hover ~ .f-select__indicator,
    .has-error .f-select__field:focus ~ .f-select__indicator {
      color: #e91e63 !important; }
    .f-select__field[disabled].f-select__field[disabled] ~ .f-select__indicator {
      color: silver; }
  .f-select__message {
    font-size: 0.8em; }
    .f-select__message--instruction {
      color: gray; }
    .f-select__message--error {
      color: #e91e63; }
      .has-error .f-select__message--error::-moz-selection {
        color: white;
        background: #e91e63 !important;
        text-shadow: none; }
      .has-error .f-select__message--error::selection {
        color: white !important;
        background: #e91e63 !important;
        text-shadow: none; }
    .f-select__message--error + .f-select__message--instruction {
      display: none; }
  .f-select__arrow:after {
    content: '';
    position: absolute;
    top: 50%;
    transform-origin: center right;
    pointer-events: none;
    transition: border-color 0.2s cubic-bezier(0.51, 0.92, 0.24, 1); }
  .f-select__arrow:after {
    right: 1.125em;
    transform: translateY(-50%) rotate(45deg);
    border-style: solid;
    border-width: 0 2px 2px 0;
    width: 0.5em;
    height: 0.5em;
    display: block;
    border-color: grey; }
    .f-select__field:hover ~ .f-select__arrow:after {
      border-color: #6fcf97; }
    .f-select__field:focus ~ .f-select__arrow:after {
      border-color: #6fcf97; }
    .f-select__field[disabled] ~ .f-select__arrow:after {
      border-color: silver; }
    .f-select.has-error .f-select__field:hover ~ .f-select__arrow:after,
    .f-select.has-error .f-select__field:focus ~ .f-select__arrow:after {
      border-color: #e91e63; }
